import app from 'flarum/forum/app';
import Component from 'flarum/common/Component';
import Avatar from 'flarum/common/components/Avatar';
import username from 'flarum/common/helpers/username';
import Link from 'flarum/common/components/Link';
/**
 * The `SupporterCard` component displays a user's avatar and username
 * in a card format for the supporters page.
 *
 * For featured supporters (monthly), the bio is also displayed if available.
 */
export default class SupporterCard extends Component {
  view(vnode) {
    const user = this.attrs.user;
    const featured = this.attrs.featured || false;
    return m("div", {
      className: "SupporterCard".concat(featured ? ' SupporterCard--featured' : '')
    }, m(Link, {
      href: app.route.user(user),
      className: "SupporterCard-link"
    }, m("div", {
      className: "SupporterCard-avatar"
    }, m(Avatar, {
      user: user,
      loading: "lazy"
    })), m("div", {
      className: "SupporterCard-info"
    }, m("h3", {
      className: "SupporterCard-username"
    }, username(user)), featured && this.renderBio(user))));
  }
  renderBio(user) {
    // Try to get formatted HTML bio first (if fof/user-bio with formatting is enabled)
    const bioHtml = user.attribute('bioHtml');
    if (bioHtml && bioHtml.trim()) {
      return m("div", {
        className: "SupporterCard-bio"
      }, m.trust(bioHtml));
    }

    // Fall back to plain text bio
    const bio = user.attribute('bio');
    if (bio && bio.trim().length > 0) {
      // Simple sanitization: escape HTML and convert newlines to <br>
      const sanitizedBio = bio.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;').replace(/\n/g, '<br>');
      return m("div", {
        className: "SupporterCard-bio"
      }, m.trust(sanitizedBio));
    }
    return null;
  }
}
flarum.reg.add('flarum-discuss', 'forum/components/SupporterCard', SupporterCard);